<template>
  <div class="admin">
    <router-link to="/add">
      <el-button type="primary">添加项目</el-button>
    </router-link>
    <router-link to="/addimg">
      <el-button type="primary">添加图片</el-button>
    </router-link>
    <span class="type-select">
      <el-select v-model="value" placeholder="请选择" @change="selectChange">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
    </span>
    <router-link to="imgbox">
      <el-button type="primary">图片库</el-button>
    </router-link>
    <div class="my-table">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="id" label="ID" width="180">
        </el-table-column>
        <el-table-column prop="title" label="标题" width="180">
        </el-table-column>
        <el-table-column prop="author" label="作者">
        </el-table-column>
        <el-table-column prop="type" label="类型">
           <template slot-scope="scope">
              <span>{{proType[scope.row.type-1]}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="listImg" label="列表图片">
          <template slot-scope="scope">
              <router-link :to="{name:'ImgBox',query:{id:scope.row.id,type:1}}">
                <el-button type="text" size="small">添加</el-button>
              </router-link>
              <el-button type="text" size="small" @click="showImg(scope.row.listImg)">查看</el-button>
            <!-- <a :href="'http://go.limingyi666.com/'+scope.row.listImg" target="view_window">{{scope.row.listImg}}</a> -->
          </template>
        </el-table-column>
        <el-table-column prop="introImg" label="介绍图片">
          <template slot-scope="scope">
              <router-link :to="{name:'ImgBox',query:{id:scope.row.id,type:2}}">
                <el-button type="text" size="small">添加</el-button>
              </router-link>
              <el-button type="text" size="small" @click="showImg(scope.row.introImg)">查看</el-button>
            <!-- <a :href="'http://go.limingyi666.com/'+scope.row.introImg" target="view_window">{{scope.row.introImg}}</a> -->
          </template>
        </el-table-column>
        <el-table-column prop="images" label="内容图片">
          <template slot-scope="scope">
              <router-link :to="{name:'ImgBox',query:{id:scope.row.id,type:0}}">
                <el-button type="text" size="small">添加</el-button>
              </router-link>
              <el-button type="text" size="small" @click="showImg(scope.row.images)">查看</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="images" label="操作">
            <template slot-scope="scope">
                <el-button type="text" size="small" @click="delVod(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
      </el-table>
    </div>
    <el-dialog title="图片展示" :visible.sync="dialogTableVisible" width="80%">
      <el-row>
        <el-col :span="24" v-for="item in imgGroup" :key="item">
          <el-card :body-style="{ padding: '0px' }" >
            <img class="show-img" :src="`http://go.limingyi666.com/${item}`">
          </el-card>
      </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HelloWorld",
  data() {
    return {
      dialogTableVisible: false,
      imgGroup: [],
      proType: ["品牌", "UI", "网页", "H5", "平板"],
      tableData: [],
      options: [
        {
          value: "1",
          label: "1"
        },
        {
          value: "2",
          label: "2"
        }
      ],
      value: ""
    };
  },
  methods: {
    _request: function(type) {
      var self = this;
      var url;
      if (type) {
        url = `/getPages?size=100&type=${type}`;
      } else {
        url = "/getPages?size=100";
      }
      axios.get(url).then(function(res) {
        console.log(res);
        self.tableData = res.data;
      });
    },
    selectChange: function(val) {
      this._request(val);
    },
    showImg(img) {
      this.dialogTableVisible = true;
      if (typeof img === "string") {
        this.imgGroup = [img];
      } else {
        this.imgGroup = img;
      }
    },
    delVod(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(async () => {
          const res = await axios.post("/delVod", {
            Id: id
          });
          this._request();
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  },
  created: function() {
    this._request();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.show-img {
  width: 100%;
}
.type-select {
  margin-left: 30px;
}

.my-table {
  margin-top: 30px;
}

.list-img {
  width: 30px;
  height: 30px;
  margin-left: 20px;
}
</style>



